@import '../sass/variables';
@import '../sass/mixins';

.greetings {
    h5 {
        @include font(24px, 500, #3A3A3A);
    }

    p {
        @include font(1rem, 400, rgba(58, 58, 58, 0.6));
    }
}

.package-name {
    @include display(center);
    align-items: center;
    gap: 12.25px;
    background-color: rgba(221, 176, 91, 0.1);
    border-radius: 20px;
    color: #DDB05B;

}

@media screen and (max-width: 980px) {

    .greetings{
        h5{
            @include font(1rem, 500, #3A3A3A);
            margin-bottom: 8px;
        }
        p{
            @include font(14px, 400, #3A3A3A99);
            margin-bottom: 12.5px;
        }
        img{
            width: 60px;
            height: 60px;
        }
    } 
    .package-name {
        @include display(left);
        gap: 4px;
    }
}