@import '../../../sass/variables';
@import '../../../sass/mixins';

.settingsLinks {
    display: flex;
    flex-direction: column;
    gap: 37px;
    margin-bottom: 10px;
    margin-top: 26.5px;

    a {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-weight: 400;
        color: #3A3A3A;
        text-decoration: none;

        >span {
            display: flex;
            align-items: center;
            gap: 8px;
        }
    }
}

@media screen and (max-width: 980px) {
    .mobile-profile {
        >h4 {
            @include font(18px, 500, #3A3A3A);
            text-align: center;
            margin-bottom: 32px;
        }
    }

    .profile-banner {
        display: flex;
        align-items: center;

        >span {
            h1 {
                @include font(1rem, 500, #3A3A3A);
            }

            h2 {
                @include font(14px, 400, #3A3A3A99);
            }
        }
    }

    .my-profile{
        >h1{
            @include font(1rem, 500, #3A3A3A);
        }
    }
}